/*}*/

.circleImg{
    margin: 0 auto;
    width: 100px;
    height: 100px;
    border: #0073E6 1px solid;
    border-radius: 50%;
}
.footerSection{
    margin-top: 5rem;
    box-shadow:0 0 4px 0 rgba(0,115,230,0.2) ;

}
.socialLink{
    text-decoration: none;
    color: #0073E6;
    font-family: 'Raleway',sans-serif;
    font-size: 14px;
    font-weight: 600;
}
.socialLink:hover{
    text-decoration: none;
    color: #000000;

}

.footerLink{
    text-decoration: none;
    font-family: 'Raleway',sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #4f5050;
}
.footerLink:hover {
    text-decoration: none;
    color: #0073E6;

}
.copyrightSection{
    padding: 20px;
    background-color: #0073E6;

}
.copyrightLink{
    font-family: 'Montserreat', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: white;
    text-decoration: none;
}
.copyrightLink:hover{

    color: #4f5050;

}
.topFixedPage{
    background-image: url("../image/topBanner.jpg");
    min-height: 280px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}
.topPageOverlay{
    position: absolute;
    min-height: 280px;
    width: 100%;
    background-color: rgba(0,0,0,0.7);
}
.topPageContent{
    margin-top: 9rem;

}
.topPageTitle{
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    color: white;
    font-size: 28px;
}
.form-control:focus{
    border-radius: 2px !important;
    border-color: #0073E6 !important;
    box-shadow: 0 0 4px 0 rgba(0,115,230,0.2) !important;
}
.linkButton{
    text-decoration: none;
    color: white;
}
.linkButton:hover{
    text-decoration: none;
    color: #d7d7d7;
}
.projectImage{
    width: 90%;
    max-height: 450px !important;
}
.course-player{
    height: auto;
    width: auto;
    position: relative !important;
}